<template>
    <div class="conf-approval conf-approvalN">
      <section class="info" :style="{backgroundImage: 'url('+note+')'}">
        <div class="header">
          <img :src="form.logo || logo" alt="">
          <div class="title">公益护照</div>
          <div>Passport for Charity & Voluntary Services</div>
        </div>
        <!-- <div class="center">
            <div class="item row">
                <div class="col-sm col-sm-5 t">姓名/Name</div>
                <div class="col-sm col-sm-6 t r">护照编号/Passprot No.</div>
                <div class="col-sm col-sm-5 b">{{form.realName}}</div>
                <div class="col-sm col-sm-6 b r">{{form.licenseCode}}</div>
            </div>
            <div class="item row">
                <div class="col-sm col-sm-5 t">性别/Sex</div>
                <div class="col-sm col-sm-6 t r">出生日期/Date of birth</div>
                <div class="col-sm col-sm-5 b">{{form.sex}}</div>
                <div class="col-sm col-sm-6 b r">{{form.birthday | date}}</div>
            </div>
            <div class="item row">
                <div class="col-sm col-sm-5 t">国籍/Nationality</div>
                <div class="col-sm col-sm-6 t r">签发单位/Authority</div>
                <div class="col-sm col-sm-5 b">{{form.nationality}}</div>
                <div class="col-sm col-sm-6 b r">{{form.issued}}</div>
            </div>
        </div> -->
        <div class="center">
          <div class="top item row">
            <div class="col-sm col-sm-8 l">
              <div class="row">
                <div class="item col-sm col-sm-12">姓名/Name <br><span class="b">{{form.realName}}</span></div>
                <div class="item col-sm col-sm-6 clearMb">性别/Sex<br><span class="b">{{form.sex}}</span></div>
                <div class="item col-sm col-sm-6 clearMb">国籍/Nationality<br><span class="b">{{form.nationality}}</span></div>
              </div>
            </div>
            <div class="col-sm col-sm-4 r">
              <img class="photo" :src="form.headUrl" alt="">
            </div>
          </div>
          <div class="item row">
            <div class="col-sm col-sm-6">出生日期/Date of birth<br><span class="b">{{form.birthday | date}}</span></div>
            <div class="col-sm col-sm-6">护照编号/Passprot No.<br><span class="b">{{form.licenseCode}}</span></div>
          </div>
          <div class="item">
            <div>签发单位/Authority<br><span class="b">{{form.issued}}</span></div>
          </div>
        </div>
        <div class="photo-wrap clearfix">
          <div id="qrcode" class="photo"></div>
          <div class="m-tip">请截图保存，以便离线使用</div>
        </div>
        <div class="button-btn-wr">
          <!-- 自动审核不需要纸质 -->
          <button v-if="form.type === 0" class="button-btn-wr-cancel" @click="go">返回</button>
          <!-- 自动审核需要纸质 -->
          <div v-else class="value-wrap">
              <button class="submit-btn-2" @click="jump(form)">申领纸质护照</button>
              <button class="submit-btn-2" @click="go">返回</button>
          </div>
        </div>
      </section>
    </div>
</template>

<script>
import QRCode from 'qrcodejs2';
export default {
    data () {
        return {
            logo: require('@/assets/images/default-logo.png'),
            note: require('@/assets/images/approval-bg.jpg'),
            form: {},
            makeStatus: null,
            code: null
        };
    },
    created () {
        this.code = this.$route.query.licenseCode;
    },
    mounted () {
        this.getInfo();
    },
    methods: {
        go () {
            this.$router.go(-1);
        },
        qrcodeF () {
            let qrcode = new QRCode('qrcode', {
                width: 100,
                height: 100, // 高度  [图片上传失败...(image-9ad77b-1525851843730)]
                text: this.form.licenseCode // 二维码内容
            });
            console.log(qrcode);
        },
        getInfo () {
          this.$http.post(this.$api.passport.queryLicenseByCode, {licenseCode: this.code}).then(res => {
            res.data.code === 0 && (this.form = res.data.data);
            this.qrcodeF();
          });
        },
        jump (item) {
            // 不是从申请线过来 直接走的我的证照线
            if (item.makeStatus == 10 || item.makeStatus == 11 || item.makeStatus == 20) {
                // 制证中
                this.$router.push({name: 'certificate', query: {type: '0', licenseCode: this.form.licenseCode}});
                return false;
            }
            if (item.makeStatus == 30) {
                // 已发证
                this.$router.push({name: 'certificate', query: {type: '1', licenseCode: this.form.licenseCode}});
                return false;
            }
            localStorage.setItem('licenseCode', this.form.licenseCode);
            this.$router.push({name: 'applyForm', query: {urlStyle: item.urlStyle, licenseCode: this.form.licenseCode, entrance: this.form.entrance}});
        }
    }
};
</script>
<style lang="scss" scoped>
  .conf-approvalN {
    height: initial;
  }
  .center {
    .top {
      .r {
        text-align: right;
        .photo {
          width: px2rem(100);
          height: px2rem(120);
          background-color: #fff;
        }
      }
    }
    .cen {
      margin: 20px 0;
    }
    .bot {
      margin-bottom: 10px;
    }
  }
  .clearMb {
    margin-bottom: 0 !important;
  }
  .button-btn-wr {
    text-align: center;
    margin-top: 20px;
    button {
      line-height: 2rem;
      font-size: 16px;
      color: #fff;
      border-radius: 3px;
      border: 0;
      padding: 0;
      background-color: #72b81e;
      border-radius: 25px;
      font-size: 16px;
      padding: 0;
    }
    .button-btn-wr-cancel {
      width: 88%;
      margin: 0 auto;
      height: 2rem;
    }
    .value-wrap {
      display: flex;
      width: 89%;
      margin: 0 auto;
      button {
        width: 100%;
      }
      padding-bottom: 20px;
    }
    .value-wrap  button:nth-child(1) {
      margin-right: 1.25rem;
    }
  }
</style>



