<template>
    <div class="conf-index">
      <section class="lc-tips">
          <i class="iconfont icon-jay"></i>对个人信息有异议的去个人资料页面修改
      </section>
      <section class="card">
          <div class="header">
            <div class="title">证照名称：公益护照</div>
            <div class="b">签发机构：{{form.orgName}}</div>
          </div>
          <div class="center">
            <div>姓名：<span>{{info.realName}}</span></div>
            <div>{{info.cardType}}：<span>{{info.cardNo}}</span></div>
            <div>手机：<span>{{info.mobile}}</span></div>
          </div>
      </section>
      <section class="promise">
        <div class="l" @click="click">
          <div class="round1" :class="{'active': status}"></div>
        </div>
        <div class="r">本人承诺在申请护照时候所填信息的真实性，理解并同意<span @click="jump">《证照领取说明》</span></div>
      </section>
      <button class="submit-btn-1" @click="submit">提 交</button>
    </div>
</template>

<script>
export default {
    data () {
        return {
            status: false,
            personFirstAccount: null,
            form: {},
            info: {}
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            let pfa = localStorage.getItem('personFirstAccount');
            pfa && (this.personFirstAccount = pfa);
            this.form = this.$route.query;
            this.$http.post(this.$api.passport.queryPersonInfo, {personFirstAccount: this.personFirstAccount}).then(res => {
                if (res.data.code === 0) {
                    this.info = res.data.data;
                }
            });
        },
        click () {
            this.status = !this.status;
        },
        jump () {
            this.$router.push({name: 'explain', query: {explain: this.$route.query.explain}});
        },
        submit () {
            if (!this.status) {
                this.$mint.Toast({
                    message: '请理解并同意《证照领取说明》',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            let params = {
                issued: this.form.orgName,
                templateCode: this.form.templateCode,
                personFirstAccount: this.personFirstAccount,
                baseNo: this.form.baseNo
            };
            this.$http.post(this.$api.passport.applyLicense, params).then(res => {
                let data = res.data.data;
                if (res.data.code === 0) {
                    data['urlStyle'] = this.$route.query.urlStyle;
                    // 自动审核不需要纸质
                    localStorage.setItem('licenseCode', data.licenseCode);
                    if (data.autoStatus === 10 && data.paper === -10) {
                        data['type'] = 0;
                        this.$router.push({name: 'approvaNoNl', query: {'licenseCode': data.licenseCode}});
                    } else if (data.autoStatus === 10 && data.paper === 10) {
                        data['type'] = 1;
                        this.$router.push({name: 'approvaNoNl', query: {'licenseCode': data.licenseCode}});
                    } else {
                        this.$router.push({name: 'audit'});
                    }
                } else {
                    this.$mint.Toast({
                        message: res.data.msg,
                        position: 'top',
                        duration: 1000
                    });
                }
            });
        }
    }
};
</script>



