<template>
    <section class="license-paper">
        <div class="form">
          <span class="item link row">
            <div class="col-sm col-sm-5 l">选择领取方式</div>
            <div class="col-sm col-sm-7 r">
              <span>中国邮政证照专递</span>
              <i class="iconfont icon-arrow-right col-sm col-sm-2"></i>
            </div>
          </span>
          <div class="item">
            <div class="col-sm col-sm-4 l">收件人姓名</div>
            <div class="col-sm col-sm-4 r"></div>
          </div>
          <div class="item">
            <div class="col-sm col-sm-4 l">收件人手机</div>
            <div class="col-sm col-sm-4 r"></div>
          </div>
          <span class="item link row">
            <div class="col-sm col-sm-6 l">收件人地址</div>
            <div class="col-sm col-sm-6 r">
              <i class="iconfont icon-arrow-right col-sm col-sm-2"></i>
            </div>
          </span>
          <textarea class="textarea" cols="30" rows="10" placeholder="请输入详细地址"></textarea>
        </div>
        <!-- 重要提示 -->
        <div class="lc-tips">
          <p><i class="iconfont icon-jay"></i>重要提示</p>
          <p>1. 采用中国邮政证照专递服务。</p>
          <p>2. 在线支付邮政专递费用20元，收件时请查收发票。</p>
          <p>3. 收件时收件人需出示有效证件或提供证件后6位</p>
        </div>
        <div class="button-wrap">
            <button class="submit-btn">提 交</button>
        </div>
        <!-- 张样查看 -->
        <span class="link look">样张查看</span>
    </section>
</template>

<script>
export default {
    data () {
        return {
            list: [
                {
                    id: 0,
                    photo: '',
                    name: '公益护照'
                }
            ]
        };
    },
    created () {
    },
    methods: {
    }
};
</script>



