<template>
    <section class="certificate">
      <div class="certificateTop">
        <p class="title">
          <span v-if="type === '0'">制证中</span>
          <span v-else>已发证</span>
        </p>
        <img src="../assets/images/certificate-1_03.png" alt="">
        <p>首次申领</p>
        {{licenseCode}}
      </div>
      <div class="certificateForm takeTheir">
              <div class="item row">
                申领时间 :<span>{{form.paperCreateTime}}</span>
              </div>
              <div class="item row">
                申领状态 :<span>{{type === '0' ? '制证中' : '已发证'}}</span>
              </div>
              <div class="item row">
                申领方式 :<span>{{form.obtainType | obtainType}}</span>
              </div>
              <div v-if="type === '0'" class="item row" >
                收件地址 :<span>{{form.recipientAddress}}</span>
              </div>
              <div class="item row" >
                快递单号 :<span v-if="form.obtainNum">{{form.obtainNum}}</span><span v-else>5个工作日后查看快递单号</span>
              </div>
              <!-- <div class="item row">
                查看方式 :<span>我的证照>证照详情>申领纸质证照</span>
              </div> -->
      </div>
      <button class="submit-btn-1" @click="jump">首页</button>
    </section>
</template>

<script>
export default {
    data () {
        return {
            licenseCode: '',
            form: {},
            type: null
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            this.type = this.$route.query.type;
            // 制证中、已发证入口进入
            if (this.type) {
                this.licenseCode = this.$route.query.licenseCode;
            } else {
                let pfa = localStorage.getItem('licenseCode');
                pfa && (this.licenseCode = pfa);
            }
            this.$http.post(this.$api.passport.queryLicensePaperByCode, {licenseCode: this.licenseCode}).then(res => {
                if (res.data.code === 0) {
                    this.form = res.data.data;
                }
            });
        },
        jump () {
            this.$router.push({name: 'index'});
        }
    }
};
</script>
<style scoped lang="scss">
</style>



