// 引入子路由
// import Sub from '../frame/subroute.vue';

// 首页 (主页)
import index from '../views/home.vue';
// 证照 (申请)
import applyFor from '../views/applyFor.vue';
// 签发机构 (签发机构)
import organ from '../views/organ.vue';
// 信息确认
import information from '../views/information.vue';
// 申领纸质护照
import approvaNoNl from '../views/approvaNoNl.vue';
// 我的证照
import myLicense from '../views/myLicense.vue';
// 申领纸质护照
import paper from '../views/paper.vue';
// 结构
import audit from '../views/audit.vue';
// 纸质护照提交
import applyForm from '../views/applyForm.vue';
// 支付成功
import payment from '../views/payment.vue';
import certificate from '../views/certificate.vue';
import explain from '../views/explain.vue';
import proof from '../views/proof.vue';

export default [
    {
        path: '/',
        name: 'index',
        component: index,
        meta: {
            title: '证照'
        }
    },
    {
        path: '/applyFor',
        name: 'applyFor',
        component: applyFor,
        meta: {
            title: '证照申请'
        }
    },
    {
        path: '/organ',
        name: 'organ',
        component: organ,
        meta: {
            title: '签发机构'
        }
    },
    {
        path: '/information',
        name: 'information',
        component: information,
        meta: {
            title: '信息确认'
        }
    },
    {
        path: '/approvaNoNl',
        name: 'approvaNoNl',
        component: approvaNoNl,
        meta: {
            title: '证照'
        }
    },
    {
        path: '/myLicense',
        name: 'myLicense',
        component: myLicense,
        meta: {
            title: '我的证照'
        }
    },
    {
        path: '/paper',
        name: 'paper',
        component: paper,
        meta: {
            title: '申领纸质护照'
        }
    },
    {
        path: '/audit',
        name: 'audit',
        component: audit,
        meta: {
            title: '结果'
        }
    },
    {
        path: '/applyForm',
        name: 'applyForm',
        component: applyForm,
        meta: {
            title: '申领纸质护照'
        }
    },
    {
        path: '/payment',
        name: 'payment',
        component: payment,
        meta: {
            title: '支付成功'
        }
    },
    {
        path: '/certificate',
        name: 'certificate',
        component: certificate,
        meta: {
            title: '制证中'
        }
    },
    {
        path: '/explain',
        name: 'explain',
        component: explain,
        meta: {
            title: '证照领取说明'
        }
    },
    {
        path: '/proof',
        name: 'proof',
        component: proof,
        meta: {
            title: '样张查看'
        }
    }
];



