const passport = {
    apply: 'license/queryLicenseApplyHistory', // 首页
    templateList: 'license/queryLicenseTemplateList', // 模板列表
    queryIssuedOrgList: '/license/queryIssuedOrgList', // 签发机构列表
    searchIssuedOrgList: '/license/searchIssuedOrgList', // 签发机构搜索
    applyLicense: '/license/applyLicense', // 申请证照
    queryMyLicense: 'license/queryMyLicense', // 我的证照,
    verifyLicense: '/license/verifyLicense', // 审核证照
    findByParent: 'region/findByParent', // 省
    findByName: 'region/findByName', // 市县
    savePassportExpress: '/license/savePassportExpress', // 支付
    queryLicensePaperByCode: 'license/queryLicensePaperByCode', // 制证中
    findByFirstAccount: 'license/findByFirstAccount', // 用户信息
    queryPersonInfo: 'license/queryPersonInfo', // 个人信息
    queryLicenseByCode: 'license/queryLicenseByCode' // 查询证照详情
};

export default {
    passport
};



