<template>
  <div class="lice-myLicense">
        <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
          <div v-if="this.list.length !==0">
              <ul>
                <li class="row" v-for="(item, index) in list" :key="index" @click="jump(item)">
                    <div class="col-sm col-sm-3 l">
                        <img class="" :src="item.logo || logo" alt="">
                    </div>
                    <div class="col-sm col-sm-8 r">
                        <div class="t row">
                            <div class="col-sm col-sm-9 title">公益护照</div>
                            <!-- 10审核中 20有效 30已过期 40冻结 -->
                            <div v-if="item.status === 10" class="col-sm col-sm-3 status active-4">{{item.status | status}}</div>
                            <div v-if="item.status === 20" class="col-sm col-sm-3 status active-1">{{item.status | status}}</div>
                            <div v-if="item.status === 30" class="col-sm col-sm-3 status active-2">{{item.status | status}}</div>
                            <div v-if="item.status === 40" class="col-sm col-sm-3 status active-3">{{item.status | status}}</div>
                        </div>
                        <div class="c">
                            <div>证照编号 : <span>{{item.licenseCode}}</span></div>
                            <div v-if="item.status === 30"><span>有效期 : </span>{{item.createTime | date}} - {{item.validityTime | date}}</div>
                            <div v-else>
                              <span>有效期 : </span>
                              <span v-if="item.validity === 0">长期</span>
                              <span v-else>{{item.validity}}年</span>
                            </div>
                        </div>
                    </div>
                </li>
              </ul>
          </div>
          <div class="error" v-else>
            <img src="../assets/images/license-error.png" alt="">
            <p>暂无数据</p>
          </div>
        </mt-loadmore>
        <button v-if="list.length === 0" class="submit-btn" @click="goto">申请证照</button>
  </div>
</template>

<script>
export default {
    data () {
        return {
            form: {
                personFirstAccount: null,
                pageNumber: 1,
                pageSize: 10
            },
            list: [],
            logo: require('@/assets/images/default-logo.png'),
            allLoaded: false,
            bottomText: '上拉加载更多...',
            un: null
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            let pfa = localStorage.getItem('personFirstAccount');
            pfa && (this.form.personFirstAccount = pfa);
            this.un = JSON.parse(localStorage.getItem('appUserInfo')).cookieResp.un;
            this.$http.post(this.$api.passport.queryMyLicense, this.form).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.passport.queryMyLicense, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        },
        goto () {
            let token = localStorage.getItem('token');
            if (!token) {
                this.$mint.Toast({
                    message: '请先登录',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (!this.un) {
                this.$mint.Toast({
                    message: '请先实名认证',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            this.$router.push({name: 'applyFor'});
        },
        jump (item) {
            if (item.status === 30) {
                this.$mint.MessageBox({
                    title: '',
                    message: item.issued + '已过期，请重新申领',
                    showCancelButton: true
                });
                return false;
            }
            if (item.status === 10) {
                this.$mint.Toast({
                    message: '审核中的证照不能查看',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            // autoStatus 自动审核 10是 -10否
            // paper 10提供纸质 -10不提供
            // 10 -10自动审核不需要纸质
            if (item.autoStatus === 10 && item.paper === -10) {
                item['type'] = 0;
            }
            // 10 10自动审核需要纸质
            if (item.autoStatus === 10 && item.paper === 10) {
                item['type'] = 1;
            }
            item['entrance'] = 'my';
            this.$router.push({name: 'approvaNoNl', query: item});
        }
    }
};
</script>



