<template>
  <div class="">
      <img :src="urlStyle" alt="">
  </div>
</template>

<script>
export default {
    data () {
        return {
            urlStyle: null
        };
    },
    created () {
        this.urlStyle = this.$route.query.urlStyle;
    },
    methods: {
    }
};
</script>
<style scoped lang="scss">
  img {
    width: 100%;
  }
</style>



