<template>
  <div class="mui-content setting-issue license-issue prdListWrap">
      <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
        <!-- <header class="tabs" :class="{'tabs-bg':isShow}">
            <ul>
                <li class="row tabs-li-1">
                    <div class="col-sm col-sm-4 l" @click="click(0)">
                        <span>地区</span>
                        <i class="iconfont icon-arrow-down"></i>
                    </div>
                    <div class="col-sm col-sm-4 c active" @click="click(1)">
                        <span>类别</span>
                        <i class="iconfont icon-arrow-down"></i>
                    </div>
                    <div class="col-sm col-sm-4 r" @click="click(2)">
                        <span>附近</span>
                    </div>
                </li>
                <li v-if="isShow" class="row" :class="{'active': idx === index}" v-for="(item, index) in tabItem" :key="index" @click="itemClick(index)">
                    <div class="col-sm col-sm-8">{{item.title}}</div>
                    <i class="iconfont icon-tick col-sm col-sm-2"></i>
                </li>
            </ul>
        </header> -->
        <div :class="{'mask':isShow}" @click="isShow=false"></div>
        <section class="search" v-if="form.issuedType === 20">
            <input type="text" placeholder="输入要搜索的签发机构" v-model="form.searchName">
            <i class="iconfont icon-loupe" @click="search()"></i>
        </section>
        <section class="list" v-if="this.list.length !==0" :class="{'isSearch': form.issuedType === 20}">
          <ul>
              <li v-for="(item, index) in list" :key="index" @click="jump(item)">
                  <span class="row link">
                      <img class="col-sm col-sm-2 orgLogo" :src="item.orgLogo || logo" alt="">
                      <div class="col-sm col-sm-8">{{item.orgName}}</div>
                      <i class="iconfont icon-arrow-right col-sm col-sm-2"></i>
                  </span>
              </li>
          </ul>
        </section>
        <div class="error" v-else>
          <img src="../assets/images/license-error.png" alt="">
          <p>暂无数据</p>
        </div>
      </mt-loadmore>
  </div>
</template>

<script>
export default {
    data () {
        return {
            logo: require('@/assets/images/default-logo.png'),
            list: [],
            allLoaded: false,
            bottomText: '上拉加载更多...',
            tabs: [
                {
                    id: 0,
                    title: '地区'
                },
                {
                    id: 1,
                    title: '类别'
                },
                {
                    id: 2,
                    title: '附近'
                }
            ],
            tabItem: [],
            items: [
                {
                    id: 0,
                    title: '签发机构'
                },
                {
                    id: 1,
                    title: '签发机构'
                }
            ],
            form: {
                templateCode: null,
                issuedType: null,
                pageNumber: 1,
                pageSize: 10,
                searchName: null
            },
            idx: null,
            isShow: false
        };
    },
    created () {
        this.form.templateCode = this.$route.query.templateCode;
        this.form.issuedType = this.$route.query.issuedType;
        this.init();
    },
    methods: {
        // 数据列表
        init () {
            this.$http.post(this.$api.passport.queryIssuedOrgList, this.form).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        search () {
            this.$http.post(this.$api.passport.searchIssuedOrgList, this.form).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.passport.queryIssuedOrgList, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        },
        // tabs切换
        // click (type) {
        //     if (type === 0) {
        //         console.log('地区');
        //     }
        //     if (type === 1) {
        //         console.log(type);
        //         this.tabItem = this.items;
        //         this.isShow = !this.isShow;
        //     }
        //     if (type === 2) {
        //         console.log('附近');
        //     }
        //     this.idx = null;
        // },
        // tabitem 多选
        // itemClick (idx) {
        //     this.idx = idx;
        // },
        jump (item) {
            item['explain'] = this.$route.query.explain;
            item['urlStyle'] = this.$route.query.urlStyle;
            // this.$router.push({name: 'organ', query: {templateCode: item.templateCode, issued: item.orgName}});
            this.$router.push({name: 'information', query: item});
        }
    }
};
</script>
<style lang="scss" scoped>
  .license-issue {
    .isSearch {
      top: px2rem(50);
    }
    .orgLogo {
      width: 50px;
      height: 50px;
    }
  }
</style>



