<template>
  <div class="conf-audit">
      <div class="error">
        <img src="../assets/images/conf-error.png" alt="">
        <p><span>审核中</span> ，请等待，我们将通过短信通知您
            或者您可以去个人中心查看证照审核结果</p>
        <button @click="go">返回</button>
      </div>
  </div>
</template>

<script>
export default {
    data () {
        return {
            status: false
        };
    },
    created () {
    },
    methods: {
        go () {
            this.$router.push({name: 'index'});
        }
    }
};
</script>
<style lang="scss" scoped>
  .conf-audit .error{
    button {
      height: 2rem;
    }
  }
</style>



