<template>
    <section class="license-index prdListWrap">
        <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
            <div class="lc-tip row">
                <span class="block"></span>
                <span>申请历史</span>
                <span class="r">请至我的证照查看证照详情</span>
            </div>
            <ul v-if="this.list.length !==0">
              <!-- 通过 默认 -->
              <!-- 审核 active -->
              <!-- 取消 cancel -->
              <li v-for="(item, index) in list" :key="index">
                <div class="item row">
                  <div class="col-sm col-sm-2 l">
                    <img :src="item.headUrl || logo" alt="">
                  </div>
                  <div class="col-sm col-sm-10 r">
                    <div class="title row">
                      <div class="col-sm col-sm-6 l">公益护照</div>
                      <div class="col-sm col-sm-6 r" v-if="item.status === 20">状态 : <span class="status">{{item.status | status}}</span></div>
                      <div class="col-sm col-sm-6 r" v-if="item.status === 10">状态 : <span class="status active">{{item.status | status}}</span></div>
                      <div class="col-sm col-sm-6 r" v-if="item.status === -20">状态 : <span class="status cancel">{{item.status | status}}</span></div>
                    </div>
                    <div class="b">
                      <div>证照编号：{{item.licenseCode}}</div>
                      <div>签发机构：{{item.issued}}</div>
                      <div>申请日期：{{item.createTime | initDate}}</div>
                    </div>
                  </div>
                </div>
                <div class="line"></div>
              </li>
            </ul>
            <div class="error" v-else>
              <img src="../assets/images/license-error.png" alt="">
              <p>暂无数据</p>
            </div>
        </mt-loadmore>
        <button class="submit-btn" @click="jump">申请证照</button>
    </section>
</template>

<script>
export default {
    data () {
        return {
            logo: require('@/assets/images/default-logo.png'),
            form: {
                personFirstAccount: null, // '72-00001666-5'
                pageNumber: 1,
                pageSize: 10
            },
            list: [],
            allLoaded: false,
            bottomText: '上拉加载更多...',
            passport: null,
            un: null
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            let pfa = localStorage.getItem('personFirstAccount');
            pfa && (this.form.personFirstAccount = pfa);
            this.un = JSON.parse(localStorage.getItem('appUserInfo')).cookieResp.un;
            this.$http.post(this.$api.passport.apply, this.form).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.passport.apply, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        },
        jump () {
            let token = localStorage.getItem('token');
            if (!token) {
                this.$mint.Toast({
                    message: '请先登录',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (!this.un) {
                this.$mint.Toast({
                    message: '请先实名认证',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            this.$router.push({name: 'applyFor'});
        }
    }
};
</script>
<style scoped lang="scss">
.license-index {
  padding-bottom: px2rem(100);
  .lc-tip {
    position: relative;
    .r {
      position: absolute;
      right: px2rem(10);
      font-size: 14px;
      color: #808080;
      font-weight: normal;
    }
  }
  .item {
    padding: px2rem(18) px2rem(12.5);
    background-color: #fff;
    img {
      width: px2rem(50);
      height: px2rem(50);
      border-radius: 50%;
    }
    .r {
      .title {
        padding-bottom: px2rem(15);
        .l {
          font-size: 16px;
          color: #000;
        }
        .r {
          color: #808080;
          text-align: right;
          .status {
            color: #74b92d;
          }
          .active {
            color: #f59101;
          }
          .cancel {
             color: #999999;
          }
        }
      }
      .b {
        color: #808080;
        div {
          padding-bottom: px2rem(4);
        }
        div:nth-last-child(1) {
          padding-bottom: 0;
        }
      }
    }
  }
  .line {
    width: 100%;
    height: px2rem(7.5);
    background-color: #f2f2f2;
  }
  .prdListWrap {
    position: absolute;
    left:0;
    top: 0;
    width:100%;
    height:100%;
    overflow:auto;
    box-sizing:border-box;
    padding-top: px2rem(45);
    padding-bottom: px2rem(100);
    -webkit-overflow-scrolling: touch;
  }
}
</style>



