<template>
    <section class="noticeInsure claimExplain">
        <section class="entirety">
            <div class="claimText" v-html="explain"></div>
        </section>
        <div class="introBtn">
            <button class="submit-btn-1" @click="jump">我知道了</button>
        </div>
    </section>
</template>

<script>
export default {
    data () {
        return {
        };
    },
    created () {
        this.explain = this.$route.query.explain;
    },
    methods: {
        jump () {
            this.$router.go(-1);
        }
    }
};
</script>
<style scoped lang="scss">
  .claimExplain {
    padding: px2rem(22.5) px2rem(27) px2rem(12);
    background-color: #fff;
    .entirety {
      display: flex;
    }
    .i {
      display: block;
      margin-top: -2px;
    }
    .icon-jay:before {
      color: #72b81e;
  }
    .claimText {
      font-size: 14px;
      color: #999;
      vertical-align: middle;
    }
    .introBtn {
      padding: px2rem(40)  0 0;
    }
    .submit-btn-1 {
      width: 100%;
      top: 0;
      position: relative;
    }
  }
</style>



