<template>
  <section class="license-applyFor prdListWrap">
      <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
        <div class="list" v-if="this.list.length > 0">
          <ul>
              <li v-for="(item, index) in list" :key="index" @click="jump(item)">
                  <span class="row link">
                      <img class="col-sm col-sm-2" :src="item.logo || logo" alt="">
                      <div class="col-sm col-sm-8">{{item.title}}</div>
                      <i class="iconfont icon-arrow-right col-sm col-sm-2"></i>
                  </span>
              </li>
          </ul>
        </div>
        <div class="error" v-else>
          <img src="../assets/images/license-error.png" alt="">
          <p>暂无数据</p>
        </div>
      </mt-loadmore>
  </section>
</template>

<script>
export default {
    data () {
        return {
            form: {
                pageNumber: 1,
                pageSize: 10,
                personFirstAccount: null
            },
            list: [],
            logo: require('@/assets/images/default-logo.png'),
            allLoaded: false,
            bottomText: '上拉加载更多...'
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            let pfa = localStorage.getItem('personFirstAccount');
            pfa && (this.form.personFirstAccount = pfa);
            this.$http.post(this.$api.passport.templateList, this.form).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.passport.templateList, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        },
        jump (item) {
            this.$router.push({name: 'organ', query: {templateCode: item.templateCode, issuedType: item.issuedType, explain: item.explain, urlStyle: item.urlStyle, baseNo: item.baseNo}});
        }
    }
};
</script>
<style lang="scss" scoped>
  .license-applyFor {
    padding: px2rem(15);
    padding-bottom: 0;
  }
  .list {
    li {
      font-size: 16px;
      border-radius: 5px;
      padding: px2rem(10) px2rem(13);
      margin-top: 0;
      margin-bottom: px2rem(15);
      border: 1px solid #e5e5e5;
      border-radius: 5px;
      .link {
        color: #333;
        div {
          padding-left: px2rem(15);
        }
        .icon-arrow-right {
          text-align: right;
        }
        .icon-arrow-right:before {
          color: #333;
        }
      }
      img {
        width: px2rem(25);
        border-radius: 50%;
      }
    }
  }
  .prdListWrap {
    position: absolute;
    left:0;
    top: 0;
    width:100%;
    height:100%;
    overflow:auto;
    box-sizing:border-box;
    -webkit-overflow-scrolling: touch;
  }
</style>



